  }

  .icon_img {
    vertical-align: middle;
  }

  .stores_name {
    font-size: 16px;
    color: #404040;
  }

  .stores_introduce {
    font-size: 12px;
    color: #666666;
  }
  .select_institution{
    float: right;
    padding-right: 10px;
