<template>
  <div>
    <!--<el-header class="header_bg">-->
      <!--<el-breadcrumb separator-class="el-icon-arrow-right">-->
        <!--<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>-->
        <!--<el-breadcrumb-item></el-breadcrumb-item>-->
      <!--</el-breadcrumb>-->
    <!--</el-header>-->

    <div class="white_bg_color">
      <div class="regular_font online_prompt">
        <ol class="text_align_left">
          <li>
            xxxxx@xx.xx
          </li>
          <li>
            
            <el-button type="text" size="small" class="setting_invoice_info padding_top_bottom0"
                       @click="offlineRemittance()"><u></u>
            </el-button>
          </li>
        </ol>
      </div>
      <el-row>
        <el-col :span="18">
          <el-form :model="remittanceInfo" label-width="130px" class="data_form" :rules="invoiceRules" ref="invoiceInfo">

            <el-form-item label="">
              <el-radio-group v-model="remittanceInfo.commitWay">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="scanRemittance">
              <el-input v-model="remittanceInfo.invoiceName" autocomplete="off" placeholder=""
                        disabled=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="invoiceNature">
              <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed"
                :file-list="fileList">
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="remittanceInfo.contacts" autocomplete="off"
                        placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="remittanceInfo.contactTel" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="remittanceInfo.remarks"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-button size="medium" class="confirm_invoice_info" @click="confirmInfo()"></el-button>
            </el-form-item>
          </el-form>

        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    name: "OfflineSubmittedLate",
    data() {
      return {
        remittanceInfo: {
          commitWay: '',
          scanRemittance:'',
          contacts:'',
          contactTel:'',
          remarks:''
        },
        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
        invoiceRules:
          {
            invoiceName: [
              {required: true, message: '', trigger: 'blur'},
              {min: 2, max: 5, message: ' 2  5 ', trigger: 'blur'}
            ],
            // birth: [
            //   { type: 'date', required: true, message: '', trigger: 'change' }
            // ],
          }
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    methods: {
      offlineRemittance() {

      },
      confirmInfo(){

      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
    },

  }
</script>

<style scoped>
  @import "../../../styles/personal/personal-center.scss";

  .data_form {
    width: 450px;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .online_prompt {
    margin: 10px;
    background: rgba(246, 246, 246, 1);
    border: 1px solid rgba(189, 186, 187, 1);
  }

  .regular_font {
    color: #666666;
    font-size: 14px;
  }

  .setting_invoice_info {
    color: #009BDD;
    font-size: 14px;
  }

  .padding_top_bottom0 {
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .confirm_invoice_info{
    width:300px;
    background-color:#33B2E8;
    font-size: 17px;
    color: #FFFFFF;
  }
</style>




